<template>
  <div class="hello">
    <!-- <canvas id="gameCanvas" width="500px" height="300px" ref="canvas" @mousedown="move"> -->
      <!-- <div style="width:100px;height:100px;background:yellow;position:relative"></div> -->
    <!-- </canvas> -->
    <canvas id="canvas" width='500' height='400'  ref="capture" class="captureId">
    </canvas >
    <button @click="generatorImage">下载</button>
    <div id="captureContent"></div>
    <!-- <a :href="screenUrl" class="down" download="下载">下载</a> -->
     <img :src="dataURL" alt="" />
  </div>
</template>

<script>

import { fabric } from 'fabric'
import html2canvas from 'html2canvas'

export default {
  name: 'HelloWorld',
  data () {
    return {
      background: 'black',
      imgColor: 'red',
      imgX: 100,
      imgY: 100,
      imgWidth: 100,
      imgHeight: 100,
      myCanvas: '',
      dataURL: '',
      screenUrl: ''
    }
  },
  mounted () {
    // this.myCanvas = this.$refs.canvas.getContext('2d')
    // this.myCanvas.fillStyle = this.background
    // this.myCanvas.fillRect(0, 0, 500, 300)
    // this.myCanvas.fillStyle = this.imgColor
    // this.myCanvas.fillRect(100, 100, 100, 100)
    // document.onmouseup = function () {
    //   document.onmousemove = null
    // }
    // document.onmouseup = function (e) {
    //   document.onmousemove = null
    //   document.onmouseup = null
    // }
    const canvas = new fabric.Canvas('canvas')
    // const rect = new fabric.Rect({
    //   top: 50,
    //   left: 100,
    //   width: 100,
    //   height: 70,
    //   fill: 'red'
    // })
    fabric.Image.fromURL('../../static/s1.jpg', function (oImg) {
      oImg.scale(0.3)// 图片缩小10倍
      canvas.add(oImg)
    })
    fabric.Image.fromURL('../../static/s2.jpg', function (oImg) {
      oImg.scale(0.3)// 图片缩小10倍
      canvas.add(oImg)
    })
    // canvas.add(rect)
  },
  methods: {
    generatorImage () {
      html2canvas(this.$refs.capture).then(canvas => {
        let captureContent = document.getElementById('captureContent')
        captureContent.appendChild(canvas) // 创建一个canvas标签
        // 这里获得的是存放canvas的数组
        let canvasDataAllArr = captureContent.getElementsByTagName('canvas')
        // 每次都是在点击生成一个新的canvas标签，取最后一个
        let canvasData = canvasDataAllArr[canvasDataAllArr.length - 1]
        // console.log("canvas===", canvasData);
        var a = document.createElement('a') // 这里创建一个a标签用于后面生成png图片
        a.href = canvasData.toDataURL() // 生成图片
        a.download = 'images.png' // 图片名
        a.click() // 触发
      })
    },
    move (e) {
      // console.log(e)
      // this.myCanvas.clearRect(this.imgX, this.imgY, 100, 100)
      // this.myCanvas.fillStyle = this.background
      // this.myCanvas.fillRect(0, 0, 500, 300)
      // this.imgX = e.offsetX - this.imgWidth / 2
      // this.imgY = e.offsetY - this.imgHeight / 2
      // 判断鼠标是否点击在矩形内
      // if (e.clientX >= this.imgX + e.offsetX && e.clientX <= this.imgX + 100 + e.offsetX &&
      // e.clientY >= this.imgY + e.offsetY &&
      // e.clientY <= this.imgY + 100 + e.offsetY) {
      // 鼠标拖动时重绘黑色背景
      // this.myCanvas.fillStyle = this.background
      // this.myCanvas.fillRect(0, 0, 500, 300)
      // 鼠标拖动时重绘红色矩形
      // document.onmousemove = (e) => {
      //   this.myCanvas.clearRect(this.imgX, this.imgY, 100, 100)
      //   this.myCanvas.fillStyle = this.background
      //   this.myCanvas.fillRect(0, 0, 500, 300)
      //   this.imgX = e.offsetX - this.imgWidth / 2
      //   this.imgY = e.offsetY - this.imgHeight / 2
      //   this.myCanvas.fillStyle = this.imgColor
      //   this.myCanvas.fillRect(this.imgX, this.imgY, 100, 100)
      //   document.onmouseup = (e) => {
      //     document.onmousemove = null
      //     document.onmouseup = null
      //   }
      // }
      // this.myCanvas.fillStyle = this.imgColor
      // this.myCanvas.fillRect(this.imgX, this.imgY, 100, 100)
      // 记录矩形的位置
      // this.imgX = e.clientX - this.imgX
      // this.imgY = e.clientY - this.imgY
      // document.onmouseup = function () {
      //   document.onmousemove = null
      // }
      // }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
canvas {
  border: 1px dashed black;
  background-image: url('../../static/s3.jpg');
}
</style>



